@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;500;700;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --white: #FFFFFF;
    --dark: #131921;
}

html {
    font-size: 16px;
}

body {
    font-family: 'Nunito Sans', sans-serif;
    
    max-width: 100vw;
    height: 100vh;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';
}
